import InitFocus from "./model";

let modelInstance = new Map();

export default {
  beforeMount: (el, binding, vNode = null) => {
    const { uid: id } = vNode.ctx;
    if (!modelInstance.has(id)) modelInstance.set(id, new InitFocus({ el }));
  },
  updated: (_, binding, vNode) => {
    const { uid: id } = vNode.ctx;
    if (modelInstance.has(id)) {
      const currInstance = modelInstance.get(id);
      currInstance?.init(binding?.value || {});
    }
  },
  unmounted: (el, binding, vNode) => {
    const { uid: id } = vNode.ctx;
    const currInstance = modelInstance.get(id);
    currInstance?.removeListener();
    currInstance?.reset();
    modelInstance.delete(id);
  }
};
